@import url(https://fonts.googleapis.com/css?family=Merriweather:100,200,300,300i,400,400i,700);
@import url(https://fonts.googleapis.com/css?family=Roboto+Slab:400,700);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400);
body {
  font-family: "Open Sans", serif;
  font-size: 62.5%;
  padding: 0;
  margin: 0; }

img {
  display: block;
  width: 100%;
  height: auto; }

.wrapper {
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center; }

.form-container {
  background-color: #2c3e50;
  height: 100vh;
  padding-top: 10rem;
  padding-left: 5rem; }

.title-container {
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  background-size: cover;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center; }
  .title-container__title {
    font-size: 4rem;
    letter-spacing: 2px;
    margin: 0.5rem;
    line-height: 1.5em;
    font-family: "Roboto Slab", serif;
    text-shadow: 2px 2px #090909; }
  .title-container__subtitle {
    font-style: italic;
    font-weight: 100;
    letter-spacing: 0.1em;
    line-height: 1.5em;
    font-family: "Merriweather", serif; }

.weather-form h1 {
  color: #f3f3f3;
  font-size: 1.4rem;
  font-weight: bold;
  padding-bottom: 2rem; }

.weather-form input[type="text"] {
  background-color: transparent;
  border: 0;
  border-bottom: solid 0.1rem #f16051;
  width: 50%;
  padding-bottom: 0.4rem;
  color: #fff !important;
  font-weight: lighter;
  letter-spacing: 0.2em;
  margin-bottom: 3rem;
  margin-right: 2rem;
  font-size: 1rem; }

.weather-form input[type="text"] {
  outline: none; }

.weather-form input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 30px #2c3e50 inset;
  -webkit-text-fill-color: #fff !important; }

.weather-form button {
  border: 0;
  padding: 0.8rem 2rem;
  margin: 0 0.2rem;
  border-radius: 0.2rem;
  font-weight: lighter;
  letter-spacing: 0.1em;
  font-size: 1rem;
  cursor: pointer;
  background-color: #f16051;
  color: #fff;
  font-weight: 100; }

.weather-form button:active {
  outline: none; }

.weather__info {
  width: 60%;
  font-size: 1rem;
  font-weight: 200;
  letter-spacing: 0.2em; }
  .weather__info .weather__key {
    color: #f16051;
    border-bottom: solid 2px rgba(255, 255, 255, 0.06);
    padding: 1.4rem 0;
    font-weight: 400; }
  .weather__info .weather__key:last-child {
    border: 0; }
  .weather__info .weather__value {
    display: inline-block;
    color: #fff;
    font-weight: 200; }
  .weather__info .weather__error {
    color: #f16051;
    font-size: 1.2rem;
    letter-spacing: 0.1em;
    font-weight: 200; }

.weatherIcon {
  display: inline-block;
  color: #f3f3f3;
  font-size: 0.4rem;
  margin: 0;
  padding: 0;
  width: 20%;
  vertical-align: middle; }
  .weatherIcon.weatherIcon__icon {
    margin: 0.2rem 0;
    border-radius: 0.2rem;
    background-color: #9c9c9c; }

